import React from "react";
import { useContext } from "react";
import { SneakerContext } from "../components/context";
const getUnique = (items, value) => {
  return [...new Set(items.map((item) => item[value]))];
};
function SneakerFilter({ sneakers }) {
  const context = useContext(SneakerContext);
  const { handleChange, type, price, minPrice, maxPrice } = context;
  let types = getUnique(sneakers, "type");
  types = ["all", ...types];
  types = types.map((item, index) => {
    return (
      <option value={item} key={index}>
        {item}
      </option>
    );
  });
  return (
    <section className="filter_container">
      <form className="form_container">
        <div className="form_group">
          <label htmlFor="type">shoe type</label>
          <select
            name="type"
            id="type"
            value={type}
            onChange={handleChange}
            className="form_control"
          >
            {types}
          </select>
        </div>
        <div className="form_group">
          <label htmlFor="price">price: ${price}</label>
          <input
            type="range"
            name="price"
            min={minPrice}
            max={maxPrice}
            id="price"
            value={price}
            onChange={handleChange}
            className="form_control"
          ></input>
        </div>
      </form>
    </section>
  );
}

export default SneakerFilter;
