import React from "react";
import image from "../components/images/image.jpg";
import image2 from "../components/images/image2.jpg";

import { Link } from "react-router-dom";

function Trending({ children }) {
  return (
    <>
      <h2 className="trending_title">Trending</h2>
      <div className="trending_container">
        <div className="image_container">
          <img className="trending_image" src={image} alt="sneaker" />
          <div className="text">Sneaker sale 70% off</div>
          {children}
        </div>
        <div className="image_container">
          <img className="trending_image" src={image2} alt="sneaker" />
          <div className="text2">The Veritas</div>
          <Link to="/sneakers/:veritas" className="btn2">
            shop
          </Link>
        </div>
      </div>
    </>
  );
}

export default Trending;
