import React, { Component } from "react";
import { SneakerContext } from "./context";
import Sneaker from "./Sneaker";

class Featured extends Component {
  static contextType = SneakerContext;

  render() {
    let { featuredSneakers: sneakers } = this.context;
    sneakers = sneakers.map((sneaker) => {
      return <Sneaker key={sneaker.id} sneaker={sneaker} />;
    });

    return (
      <>
        <h2 className="featured_title">Featured</h2>
        <div className="featured_container">{sneakers}</div>
      </>
    );
  }
}

export default Featured;
