import React from "react";
import { Link } from "react-router-dom";

function Footer() {
  return (
    <section className="banner">
      <banner className="banner_wrapper">
        <h1 className="banner_title">Check out more cheap sneaker deals</h1>
        <Link to="/Sneakers" className="banner_button">
          Learn more
        </Link>
      </banner>
    </section>
  );
}

export default Footer;
