import React, { Component } from "react";
import items from "./data";

const SneakerContext = React.createContext();
class SneakerProvider extends Component {
  state = {
    sneakers: [],
    sortedSneakers: [],
    featuredSneakers: [],
    type: "all",
    price: 0,
    minPrice: 0,
    maxPrice: 0,
  };

  componentDidMount() {
    let sneakers = this.formatData(items);
    let featuredSneakers = sneakers.filter(
      (sneaker) => sneaker.featured === true
    );
    let maxPrice = Math.max(...sneakers.map((item) => item.price));

    this.setState({
      sneakers: sneakers,
      featuredSneakers: featuredSneakers,
      sortedSneakers: sneakers,
      price: maxPrice,
      maxPrice,
    });
  }

  formatData(items) {
    let tempItems = items.map((item) => {
      let id = item.id;
      let images = item.fields.image.map((image) => image.url);
      let sneakerObj = { ...item.fields, images: images, id };
      return sneakerObj;
    });
    return tempItems;
  }

  getItem = (slug) => {
    let tempSneakers = [...this.state.sneakers];
    const sneaker = tempSneakers.find((sneaker) => sneaker.slug === slug);
    return sneaker;
  };

  handleChange = (event) => {
    const target = event.target;
    const value = event.target === "checkbox" ? target.checked : target.value;
    const name = event.target.name;
    this.setState({ [name]: value }, this.filterSneakers);
  };

  filterSneakers = () => {
    let { sneakers, type, price, minPrice, maxPrice } = this.state;

    // all sneakers
    let tempItems = [...sneakers];

    // string to int
    price = parseInt(price);

    // filter by type

    if (type !== "all") {
      tempItems = tempItems.filter((sneaker) => sneaker.type === type);
    }

    // filter by price

    tempItems = tempItems.filter((item) => item.price <= price);

    // change state

    this.setState({ sortedSneakers: tempItems });
  };
  render() {
    return (
      <SneakerContext.Provider
        value={{
          ...this.state,
          getItem: this.getItem,
          handleChange: this.handleChange,
        }}
      >
        {this.props.children}
      </SneakerContext.Provider>
    );
  }
}

const SneakerConsumer = SneakerContext.Consumer;

export { SneakerProvider, SneakerConsumer, SneakerContext };
