import { HashRouter as Router, Link, Route, Switch } from "react-router-dom";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import { SneakerProvider } from "./components/context";
ReactDOM.render(
  <SneakerProvider>
    <React.StrictMode>
      <Router>
        <App />
      </Router>
    </React.StrictMode>
  </SneakerProvider>,

  document.getElementById("root")
);

