import React from "react";
import SneakerFilter from "../components/SneakerFilter";
import SneakerList from "../components/SneakerList";
import { SneakerConsumer } from "../components/context";

function SneakerContainer() {
  return (
    <SneakerConsumer>
      {(value) => {
        const { sortedSneakers, sneakers } = value;

        return (
          <div>
            <SneakerFilter sneakers={sneakers} />
            <SneakerList sneakers={sortedSneakers} />
          </div>
        );
      }}
    </SneakerConsumer>
  );
}

export default SneakerContainer;
