import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";

function Sneaker({ sneaker }) {
  const { slug, images } = sneaker;
  return (
    <article className="sneaker_container">
      <div className="img_container">
        <img className="sneakerImg" src={images[0]} alt="sneaker" />

        <Link to={`/sneakers/${slug}`} className="featured_link">
          Shop
        </Link>
      </div>
    </article>
  );
}

export default Sneaker;

Sneaker.propTypes = {
  sneaker: PropTypes.shape({
    slug: PropTypes.string.isRequired,
    images: PropTypes.arrayOf(PropTypes.string).isRequired,
  }),
};
