import React, { Component } from "react";
import { Link } from "react-router-dom";
import { SneakerContext } from "../components/context";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

class SingleItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      slug: this.props.match.params.slug,
    };
  }

  static contextType = SneakerContext;
  render() {
    const { getItem } = this.context;
    const sneaker = getItem(this.state.slug);
    if (!sneaker) {
      return (
        <div>
          <h3>no items to display</h3>
          <Link to="/sneakers" className="btn-primary">
            Return
          </Link>
        </div>
      );
    }
    const { title, description, price, image } = sneaker;

    return (
      <>
        <Slider>
          {image.map((item, index) => {
            return (
              <img
                className="single_sneaker_img"
                key={index}
                src={item.url}
                alt={title}
              />
            );
          })}
        </Slider>
        <div className="sneaker_wrap_container">
          <div className="sneaker_images_container">
            {image.map((item, index) => {
              return (
                <img
                  className="single_sneaker_img"
                  key={index}
                  src={item.url}
                  alt={title}
                />
              );
            })}
          </div>

          <div className="single_sneaker_info">
            <div className="sneaker_info">
              <h6>Men's Shoe</h6>
              <h2>${price}</h2>
            </div>
            <h1>{title}</h1>
            <p className="description">{description}</p>
            <button className="sneaker_button">Add to Cart</button>
            <button className="like_button">Favorite</button>
          </div>
        </div>
      </>
    );
  }
}

export default SingleItem;
